$메인칼라 : #ff0000;

.red {
    color: $메인칼라;
}

@mixin 함수() {

    background-color: #eee;
    padding: 20px;
    border-radius: 5px;
    width: 100%;
    margin: auto;
    display: block;
}

.my-alert {

    @include 함수();

    p {
        margin-bottom: 0;
    }
}

.my-alert2 {
    @extend .my-alert;
    background-color: #ffe591;
}

